@import (less) 'color.less';
.user-button-container {
    padding: 0;
}

[class*=" ico-"]:before {
    vertical-align: middle;
}

.user-button {
    margin: 0;
    a {
        color: @orange;
        display: table;
        padding: 0.313em 1.250em 0.313em 1.250em;
        margin: 1.250em 0 1.250em 0;
        text-decoration: none;
        cursor: pointer;
        width: 100%;
        height: 3.750em;
        &:hover,
        &:focus,
        &:active {
            background-color: @orange;
            color: @darkest_gray;
            }
        table {
            width: 100%;
            display: table-cell;
            vertical-align: middle;
        }
    }
}

.user-image-container {
    width: 1.750em;
    height: 1.750em;
    div {
        vertical-align: middle;
        font-size: 1.750em !important;
        margin: 0 !important;
    }
    img {
        position: relative;
        border-radius: 50%;
        width: 100%;
    }
}
