.user-info {
    padding-left: 0.625em;
    vertical-align: middle;
    line-height: 1;
    font-weight: normal;
}

.user-info-subtitle {
    display: inline-block;
    font-size: 0.750em;
    .user-info-subtitle--content {
        text-transform: uppercase;
        font-weight: bold;
    }
}